export default {
  data() {
    return {
      currentpwd: '',
      newpwd: '',
      renewpwd: '',
      errorInfo: ''
    }
  },
  created() {
  },
  methods: {
    clickChangeBtn() {
      if (this.currentpwd === '') {
        this.errorInfo = '*'
        return
      }
      if (this.newpwd === '') {
        this.errorInfo = '*'
        return
      }
      if (this.renewpwd === '') {
        this.errorInfo = '*'
        return
      }
      if (this.renewpwd !== this.newpwd) {
        this.errorInfo = '*'
        return
      }
      this.errorInfo = ''
      console.log()
    }
  }
}



